<template>
  <section>
    <h4 class="mt-4">
      Toast
      <small class="text-muted">
        <i18n path="dialog.demo.toastTitle" />
      </small>
    </h4>
    <div class="card p-4 shadow-sm">
      <div class="row mb-4 bg-light py-4 m-0 rounded-3">
        <div class="col-md-3">
          <div class="font-weight-bold mb-2">
            Message type
          </div>
          <div>
            <select
              v-model="toastType"
              class="form-select"
            >
              <option value="info">
                info
              </option>
              <option value="warning">
                warning
              </option>
              <option value="error">
                error
              </option>
              <option value="success">
                success
              </option>
            </select>
          </div>
        </div>
        <div class="col-md-3">
          <div class="font-weight-bold mb-2">
            Screen corner position
          </div>
          <div>
            <select
              v-model="position"
              class="form-select"
            >
              <option value="topLeft">
                topLeft
              </option>
              <option value="topCenter">
                topCenter
              </option>
              <option value="topRight">
                topRight
              </option>
              <option value="bottomLeft">
                bottomLeft
              </option>
              <option value="bottomCenter">
                bottomCenter
              </option>
              <option value="bottomRight">
                bottomRight
              </option>
            </select>
          </div>
        </div>
        <div class="col-md-3">
          <div class="font-weight-bold mb-2">
            Icon
          </div>
          <div>
            <div class="form-check">
              <input
                class="form-check-input"
                type="checkbox"
                v-model="toastIcon"
                id="toastIcon"
              >
              <label
                class="form-check-label"
                for="toastIcon"
              >
                Diaplay icon
              </label>
            </div>
          </div>
        </div>
      </div>

      <div>
        <button
          type="button"
          class="btn btn-secondary"
          @click="callToast"
        >
          <fa-icon icon="paper-plane" /> Open Toast
        </button>
      </div>

      <showcode>
        <v-code lang="javascript">
          <pre>
            // Globally instance call toast dialog
            import { DialogToast } from 'v-dialogs'
            DialogToast(message, [callback], [options])
            // Functional call toast dialog
            this.$dlg.toast(message, [callback], [options])

            // show message in toast dialog, using information type
            // and show in bottom right corner by default
            DialogToast('you got a new message!')
            // show message with close callback
            DialogToast('you got a new message!', () => {
              // toast close callback
            })
            // show message with options
            DialogToast('you got a new message!', {
              messageType: 'warning',
              closeTime: 3 // auto close dialog time(second)
            })
            // show message with close callback and options
            DialogToast('you got a new message!', () => {
              //toast close callback
            }, {
              messageType: 'warning',
              closeTime: 3
            })</pre>
        </v-code>
      </showcode>
    </div>
  </section>
</template>

<script>
import { DialogToast } from 'v-dialogs'

export default {
  data () {
    return {
      position: 'bottomRight',
      toastType: 'info',
      toastIcon: true
    }
  },
  methods: {
    callToast () {
      const { toastType, position, toastIcon } = this
      DialogToast(`This is a Toast dialog, messageType is ${toastType}`, {
        messageType: toastType,
        closeTime: 3,
        position: position,
        icon: toastIcon
      })
    }
  }
}
</script>
