<template>
  <section>
    <h4 class="mt-4">
      Modal
      <small class="text-muted">
        <i18n path="dialog.demo.modalTitle" />
      </small>
    </h4>
    <div class="card p-4 shadow-sm">
      <div class="">
        <button
          type="button"
          class="btn btn-secondary"
          @click="callModal"
        >
          <fa-icon icon="window-maximize" /> Open Modal
        </button>
      </div>

      <showcode>
        <v-code lang="javascript">
          <pre>
          // Functional call modal dialog
          import { DialogModal } from 'v-dialogs'
          DialogModal(component, [options])
          // Globally instance call modal dialog
          this.$dlg.modal(component, [options])

          // Some code for example
          import { DialogAlert, DialogModal } from 'v-dialogs'
          import Page from './Page.vue'
          export default {
            methods: {
              open () {
                DialogModal(Page, {
                  width: 350,
                  height: 450,
                  // send params to inner component
                  params: {
                    name: 'Terry Zeng'
                  },
                  // modal dialog close callback
                  // data: inner component call 'this.$emit.('close', data)'
                  // to close modal and return data
                  callback: data => {
                    // alert selected result
                    DialogAlert(`You selected ${data.companyName} company`)
                  }
                })
              }
            }
          }
          </pre>
        </v-code>
      </showcode>

      <h5 class="mt-3">
        <i18n path="dialog.demo.vuexInModal" />
      </h5>
      <showcode>
        <v-code lang="javascript">
          <pre>
          // Vuex state definition for example
          export default new Vuex.Store({
            state: {
              userName: 'Terry Zeng'
            }
          })

          </pre>
        </v-code>
        <v-code>
          <pre>
          &lt;!-- The page in Modal --&gt;
          &lt;template&gt;
            &lt;div v-text="userName"&gt;&lt;/div&gt;
          &lt;/template&gt;
          &lt;script&gt;
          import { mapState } from 'vuex'
          export default {
            computed: {
              ...mapState(['userName'])
            }
          }
          &lt;/script&gt;
          </pre>
        </v-code>
      </showcode>
    </div>
  </section>
</template>

<script>
import { DialogModal, DialogAlert } from 'v-dialogs'
import profile from './UserProfile'

export default {
  methods: {
    callModal () {
      DialogModal(profile, {
        width: 350,
        height: 450,
        title: 'User profile',
        callback: data => {
          const { companyName } = data
          DialogAlert(`The user company is: <b>${companyName || 'Not Selected'}</b>`)
        },
        params: {
          name: 'Terry Zeng'
        }
      })
    }
  }
}
</script>
