<template>
  <div class="p-3">
    <p>
      Name (received by params):
      <input
        type="text"
        class="form-control"
        :value="name"
      >
    </p>
    <p>
      Age:<input
        type="text"
        class="form-control"
        v-model="age"
      >
    </p>
    <div>
      Company:
      <div class="input-group">
        <input
          type="text"
          class="form-control"
          disabled="disabled"
          v-model="company"
          placeholder="Please choose a company"
        >
        <span class="input-group-append">
          <button
            class="btn btn-outline-secondary"
            type="button"
            @click="chooseCompany"
          >Choose</button>
        </span>
      </div>
    </div>
    <p class="mt-4">
      <button
        type="button"
        class="btn btn-primary me-3"
        @click="ok"
      >
        OK
      </button>
      <button
        type="button"
        class="btn btn-outline-dark"
        @click="viewCode"
      >
        View Code
      </button>
    </p>
  </div>
</template>

<script>
import { DialogModal, DialogToast, DialogMask, DialogHelper } from 'v-dialogs'
import Company from './Company'
import UserProfileCode from './UserProfileCode'

export default {
  props: {
    name: { type: String, default: '' }
  },
  data () {
    return {
      company: '',
      age: 20
    }
  },
  methods: {
    ok () {
      const key = DialogMask('Data saving...(sleep 3 sec)')
      setTimeout(() => {
        DialogHelper.close(key)
        this.$emit('close', {
          companyName: this.company
        })
      }, 3000)
    },
    chooseCompany () {
      // console.log(this)
      DialogModal(Company, {
        width: 500,
        height: 500,
        title: 'Company list',
        callback: data => {
          DialogToast(`Your selected <b>${data.name}</b> company.`, {
            closeTime: 2
          })
          this.company = data && data.name
        }
      })
    },
    viewCode () {
      DialogModal(UserProfileCode, {
        width: 800,
        height: 600,
        title: 'User profile code'
      })
    }
  }
}
</script>
