<template>
  <section>
    <h4 class="mt-4">
      Alert
      <small class="text-muted">
        <i18n path="dialog.demo.alertTitle" />
      </small>
    </h4>
    <div class="card p-4 shadow-sm">
      <div class="row mb-4 bg-light py-4 m-0 rounded-3">
        <div class="col-md-3">
          <div class="font-weight-bold mb-2">
            Message type
          </div>
          <div>
            <select
              v-model="alertType"
              class="form-select"
            >
              <option value="info">
                info
              </option>
              <option value="warning">
                warning
              </option>
              <option value="error">
                error
              </option>
              <option value="success">
                success
              </option>
              <option value="confirm">
                confirm
              </option>
            </select>
          </div>
        </div>
        <div class="col-md-3">
          <div class="font-weight-bold mb-2">
            Icon
          </div>
          <div>
            <div class="form-check">
              <input
                class="form-check-input"
                type="checkbox"
                v-model="alertIcon"
                id="alertIcon"
              >
              <label
                class="form-check-label"
                for="alertIcon"
              >
                Diaplay icon
              </label>
            </div>
          </div>
        </div>
      </div>

      <div>
        <button
          type="button"
          class="btn btn-secondary"
          @click="callAlert"
        >
          <fa-icon icon="exclamation-triangle" /> Open Alert
        </button>
      </div>

      <showcode>
        <v-code lang="javascript">
          <pre>
          // Function call alert dialog
          import { DialogAlert } from 'v-dialogs'
          DialogAlert(message, [callback], [options])
          // Globally instance call alert dialog
          this.$dlg.alert(message, [callback], [options])

          // show message in alert dialog, using information type by default
          DialogAlert('Data saved successfully!')
          // show message with close callback
          DialogAlert('Data saved successfully!', () => {
            // dialog close callback
          })
          // show message with options
          DialogAlert('Data saved successfully!', {
            messageType: 'success',
            language: 'en'
          })
          // show message with close callback and options
          DialogAlert('Data saved successfully!', () => {
            // dialog close callback
          }, {
            messageType: 'success',
            language: 'en'
          })
          // confirm type alert dialog will have one more button(cancel)
          DialogAlert('Do you really want to leave?', {
            messageType: 'confirm',
            cancelCallback: () => {
              // do something when you click cancel button
            }
          })
          </pre>
        </v-code>
      </showcode>
    </div>
  </section>
</template>

<script>
import { DialogAlert } from 'v-dialogs'

export default {
  data () {
    return {
      alertType: 'info',
      alertIcon: true
    }
  },
  methods: {
    callAlert () {
      const { alertType, alertIcon } = this
      const option = {
        messageType: alertType,
        icon: alertIcon
      }
      if (alertType === 'confirm') {
        option.cancelCallback = () => {
          DialogAlert('You canceled confirm dialog!')
        }
      }
      DialogAlert(`This is an Alert dialog, messageType is ${alertType}`, option)
    }
  }
}
</script>

