<template>
  <div class="p-4">
    <h2>Template</h2>
    <div class="p-3 bg-light mb-5">
      <showcode>
        <code-highlight>
          <pre>
&lt;div&gt;
  &lt;p&gt;
    User name:
    &lt;input type="text" class="form-control" v-model="userName"&gt;
  &lt;/p&gt;
  &lt;p&gt;
    Age:&lt;input type="text" class="form-control" v-model="age"&gt;
  &lt;/p&gt;
  &lt;div&gt;
    Company:
    &lt;div class="input-group"&gt;
      &lt;input
        type="text"
        class="form-control"
        disabled="disabled"
        v-model="company"
        placeholder="Please choose a company"
      &gt;
      &lt;span class="input-group-btn"&gt;
        &lt;button
          class="btn btn-default"
          type="button"
          @click="chooseCompany"
        &gt;Choose&lt;/button&gt;
      &lt;/span&gt;
    &lt;/div&gt;
  &lt;/div&gt;

  &lt;p&gt;
    &lt;button
      type="button"
      class="btn btn-default"
      @click="ok"
    &gt;OK&lt;/button&gt;
  &lt;/p&gt;
&lt;/div&gt;</pre>
        </code-highlight>
      </showcode>
    </div>

    <h2>Script</h2>
    <div class="bg-light p-3">
      <code-highlight lang="javascript">
        <pre>
        import { DialogModal } from 'v-dialogs'
        import Company from './Company.vue'

        export default {
          props: {
            name: String
          },
          data () {
            return {
              userName: this.name,
              company: '',
              age: 20
            }
          },
          methods: {
            ok () {
              // close current modal dialog and return data
              this.$emit('close', {
                userName: this.userName,
                company: this.company,
                age: this.age
              })
            },
            chooseCompany () {
              // Open a new nested modal dialog
              DialogModal(Company, {
                width: 400,
                height: 300,
                title: 'Company list',
                callback: data => {
                  this.company = data && data.name
                }
              })
            }
          }
        }
        </pre>
      </code-highlight>
    </div>
  </div>
</template>

<script>
import CodeHighlight from '@/components/v-code/Code'

export default {
  components: {
    CodeHighlight
  }
}
</script>
