<template>
  <section>
    <h4 class="mt-4">
      Helper
      <small class="text-muted">
        <i18n path="dialog.demo.helper.title" />
      </small>
    </h4>
    <div class="p-4 shadow-sm border rounded-3">
      <h5 class="font-monospace">
        close(key?: string): void
      </h5>
      <p><i18n path="dialog.demo.helper.close" /></p>
      <showcode :trigger="false">
        <v-code lang="javascript">
          <pre>
            import { DialogMask, DialogHelper } from 'v-dialogs'
            // Open a mask layer and get dialog key
            const key = DialogMask()
            doSomeJobStuff().then(() => {
              // Close mask layer by dialog key
              DialogHelper.close(key)
            })</pre>
        </v-code>
      </showcode>

      <h5 class="font-monospace mt-3">
        closeAll(): void
      </h5>
      <p><i18n path="dialog.demo.helper.closeAll" /></p>
      <showcode :trigger="false">
        <v-code lang="javascript">
          <pre>
          import { DialogHelper } from 'v-dialogs'

          fetchData()
            .then(() => {
              // Do fetch data success work
              ...
            })
            .catch(error => {
              // Login state timeout for example
              if (error.isLoginTimeout) {
                // Close all opened dialogs
                DialogHelper.closeAll()
                // Redirect to login page
                router.push({ path: '/login' })
              }
            })</pre>
        </v-code>
      </showcode>
    </div>
  </section>
</template>

<script>
export default {

}
</script>
